body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
    'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}
body {
  background-color: #9ac4f8;
}

h1 {
  text-align: center;
}
.search-bar-container {
  display: flex;
  flex-flow: row;
  justify-content: space-around;
  align-items: center;
  padding-bottom: 2rem;
}

.search-bar-button-container {
  width: 40%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.search-bar-button, .search-field-dropdown {
  background-color: #cb958e;
  height: 2rem;
  width: 5rem;
  border: 1px solid black;
  box-shadow: 1px 1px #282c34;
  margin: 0 .25rem;
}

.search-bar-button {
  border-radius: 10%;
}


#all-gifs-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: left;
}
.single-gif-container {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  border: 1px black solid;
  margin: .25rem;
  box-shadow: 1px 1px #282c34;
  background-color: #cb958e;
}

.gif-copy-url {
  font-size: 15px;
  text-align: center;
}

.gif-url {
  color: black;
  font-size: 10px;
  border: 1px black solid;
  background-color: aliceblue;
}

.landing-page-ui-description {
  text-align: center;
}
 @media only screen and (max-width: 600px){
  .App {
    display: flex;
    flex-flow: column;
  }
  #all-gifs-container {
    flex-flow: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
  }

  .search-bar-button, .search-field-dropdown {
    background-color: #cb958e;
    height: 2rem;
    width: 4rem;
    margin: 0;
  }
  #rating-label {
    display: none;
  }

  .search-bar-button-container {
    justify-content: flex-start;
    align-items: baseline;
  }
 }
